.admin-dashboard {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.admin-tabs {
  width: 100%;
  display: flex;
  justify-content: center;
  border-radius: 15px;
}
.css-1h9z7r5-MuiButtonBase-root-MuiTab-root.Mui-selected {
  background: #88cc1bcc;
  color: #fff !important;
}
.css-1aquho2-MuiTabs-indicator {
  display: none;
}
.addusercontainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 2%;
  font-family: "Inter", sans-serif;
  font-style: normal;
}

.addusercontainer .adminsidebar {
  display: flex;
  flex-direction: column;
  width: 20%;
  height: 100vh;
  justify-content: flex-start;
  align-items: center;
  gap: 3%;
  background: #777c9b;
  border-radius: 0px 60px 60px 0px;
}

.addusercontainer .adminsidebar img {
  margin: 25% 0% 5% 0%;
}

.addusercontainer .adminsidebar h3 {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
}

.addusercontainer .adminsidebar h3:hover {
  background: #aab7cb;
  color: #303939;
  width: 100%;
  text-align: center;
}

.addusercontainer .adminbody {
  display: flex;
  flex-direction: column;
  width: 80%;
  height: max-content;
}

.addusercontainer .adminbody .adminnavbar {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-top: 5%;
  padding: 1% 2%;
  background: rgba(119, 124, 155, 0.8);
}

.addusercontainer .adminbody .adminnavbar span {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #ffffff;
}

.addusercontainer .adminbody .adminnavbar h2 {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 25px;
  line-height: 30px;
  color: #ffffff;
}

.addusercontainer .adminbody .adminnavbar svg {
  width: 3.75;
  height: 3.75;
}

.addusercontainer .adminbody .adduser {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 5%;
}

.addusercontainer .adminbody .adduser .buttons {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 5%;
  justify-content: center;
  align-items: center;
}

.addusercontainer .adminbody .adduser .sections {
  display: flex;
  flex-direction: row;
  width: 85%;
  gap: 0%;
  justify-content: center;
  align-items: center;
  background: #59b919;
  border-radius: 10px;
}

.addusercontainer .adminbody .adduser .sections h4 {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  width: 100%;
  padding: 2% 1%;
  color: #263238;

  border-radius: 11px 3px 3px 11px;
}

.addusercontainer .adminbody .adduser .sections h4:hover {
  background: #777c9b;
  color: #ffffff;
}

.addusercontainer .adminbody .adduser .buttons .adduserbtn {
  width: 12rem;
  background: #aab7cb;
  border-radius: 10px;
  border: none;
  padding: 1% 2%;
  white-space: nowrap;

  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 30px;
  color: #263238;
}

.addusercontainer .adminbody .adduser .buttons .viewbtn {
  width: 12rem;
  background: #9296af;
  border-radius: 10px;
  color: #ffffff;
  border: none;
  padding: 1% 2%;
  white-space: nowrap;

  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 30px;
  color: #ffffff;
}

.addusercontainer .adminbody .adduser .buttons .viewbtn:hover {
  background: #aab7cb;
  color: #000000;
}

.addusercontainer .adminbody .adduser .addbtn {
  margin-right: 45%;

  width: 12rem;
  background: #9296af;
  border-radius: 10px;
  color: #ffffff;
  border: none;
  padding: 1% 2%;
  white-space: nowrap;
}

.addusercontainer .adminbody .adduser .tables {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

